import React from 'react';
import emailjs from 'emailjs-com';
require('dotenv').config();

function Contact(){
  function sendEmail(e) {
    e.preventDefault();
    console.log(e);

    const serviceID = process.env.REACT_APP_SERVICE_ID;
    const templateID = 'template_146w2nx';
    const userID = process.env.REACT_APP_USER_ID;
    console.log(process.env.REACT_APP_SERVICE_ID);

    emailjs.sendForm(serviceID, templateID, e.target, userID)
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
  }
  return (
    <section id="contact">

        <div className="title-form"></div>
        <h1 className="section-title">Contact me</h1>
        <div className="container container--flex">
            <form id="form"  className="contact-container" onSubmit={sendEmail}>
                <input className="contact contact--object" type="text" placeholder="Your e-mail address"></input>
                {/* <!-- <label for="object">Object</label> --> */}
                <input className="contact contact--object" type="text" placeholder="Object"></input>
                {/* <!-- <label for="message">Message</label> --> */}
                <textarea className="contact contact--content" type="text" placeholder="Your message..."></textarea>
                <input type="submit" id="button" className="btn" value="Send"/>
            </form>
            <div>
                <div className="circle circle__primary"></div>
                <div className="circle circle__secondary"></div>
                <p className="contactme-text">Don't hesitate to contact me!</p>
            </div>
        </div>
    </section>
  )
}


